@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Work+Sans:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.jumbo-text{
    color: #2f2f3f;
    font-size: 3.5rem;
    font-weight: 300;
    line-height: 1.2;
    font-family: 'Bebas Neue', sans-serif;
}

h1 {
font-family: 'Bebas Neue', sans-serif;
}

.divider{
    max-width: 70%;
    margin: auto;
}

.display-4{
    font-size: 3rem;
}

.card-text{
    font-size: 1rem;
}

.text-area{
    min-height: 100px;
    margin-top: 10px;
}

h4 {
    font-family: 'Work Sans', sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.15rem;
    color: #2f2f3f;
}

h5 {
    font-family: 'Work Sans', sans-serif;
    font-weight: 400;
    text-transform: uppercase;
    font-size: .9rem;
}


.partners-container{
    display: inline-block;
    margin: 0rem 8rem;
}
.partner-logo{
    max-height: 15rem;
    max-width: 15rem;
    width: 100%;
    padding: 2rem;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.cplus-logo{
    max-height: 15rem;
    max-width: 15rem;
    width: 100%;
    height: 80%;
    padding: 2rem;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    -webkit-transform: translate(0, 8%);
            transform: translate(0, 8%);
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.cplus-logo:hover{
    -webkit-transform: translate(0, -2%);
            transform: translate(0, -2%);
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}

.partner-logo:hover{
    -webkit-transform: translate(0, -10%);
            transform: translate(0, -10%);
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}

.headline-container{
    background-color:white;
    color: #2f2f3f;
    font-family: Bebas Neue, cursive;
}
.header-image-container{
    height: 30%;
    width: 30%;
}
.header-image{
    height: 100%;
    width:100%;
}
/* Event Card Styles */

.event-img {
    border: none;
    height: 215px;
    width: 215px;
    object-fit: cover;
    object-position: 0%;
} 

.event-title {
    font-family: 'Work Sans', sans-serif;
    color: #2f2f3f;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.15rem;
    padding-bottom: 0px;
    margin-bottom: 2px;
}
.mini-event-title {
    font-family: 'Work Sans', sans-serif;
    color: #2f2f3f;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.15rem;
    padding-bottom: 5px;
    margin-bottom: 2px;
    max-width: 230px;
    height: 120px;
}


.social-icon{
    transition: -webkit-transform .15s;
    transition: transform .15s;
    transition: transform .15s, -webkit-transform .15s;
}

.social-icon:hover{
    -webkit-transform: translate(0, -15%);
            transform: translate(0, -15%);
}

.event-speaker{
    font-family: 'Work Sans', sans-serif;
    color: #2f2f3f;
    font-weight: 400;
    text-transform: uppercase;
    font-size: .9rem;
    padding-bottom: 5px;
}

.card-body {
    font-family: 'Work Sans', sans-serif;
    font-size: .9rem;
    text-align: center;
    color: #2f2f3f;
}



.event-description{
    font-family: 'Work Sans', sans-serif;
    color: #2f2f3f;
    font-size: 1rem;
    padding-right: 0px;
    padding-bottom: 15px;
}

.calendar-icon{
    margin-top: -5px;
}

.lead {
    font-family: 'Work Sans', sans-serif;
    color: #2f2f3f;
    font-size: 1.5rem;
}

.section-title {
    color: #2f2f3f;
    margin-bottom: 1rem;
}

.our-vision{
    margin-top: 150px;
}

.bg-custom {
    background-color: white;
}

.bg-partners{
    background: rgba(248,249,250,1);
}

.bg-footer {
    background-color: #2f2f3f;
}

.footer-text {
    font-size: .9rem;
}

.disclaimer {
    font-size: .5rem;
}

/* Navbar Transition */
.navbar {
    opacity: 1;
    transition: opacity 0.5s ease;
}

.navbar-hide {
    pointer-events: none;
    opacity: 0;
}

/* Icon Backgrounds */
.dot {
    height: 110px;
    width:110px;
    display: inline-block;
    border-radius: 50%;
    border: solid 5px white;
}

.brand-img {
    max-width: 100px;
    max-height: 100px;
}
.footer-brand-img {
    max-width: 200px;
    max-height: 200px;

}

ul {
    list-style-type: none;
}

.vision-img {
    background-image: url(/thisisdesco/static/media/vision-bckg.9e74e4e4.png);
    object-fit: cover; 
}

.linkfont {
    font-family: 'Work Sans', sans-serif;
    text-transform: uppercase;
    color: #2f2f3f;
}

.nav-link{
    text-decoration: none;
    margin-right: 10px;
    line-height: 1;
    position: relative;
    z-index: 0;
    display: inline-block;
    padding: 10px 0px;
    overflow: hidden;
    color: #2f2f3f;
    vertical-align: bottom;
    transition: color .3s ease-out;
}

.nav-link:hover{
    cursor: pointer;
    font-weight: bold;
    color: #2f2f3f;
}



.btn {
    font-family: 'Work Sans', sans-serif;
    text-transform: uppercase;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.btn:hover{
    -webkit-transform: translate(0, -10%);
            transform: translate(0, -10%);
}

.btn-outline-danger:hover{
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    background: none;
    cursor: default;
    color: #dc3545;
}

.btn-outline-danger:active{
    background: none;
    cursor: default;
    color: #dc3545;
}


main {
  transition: margin-top 0.2s ease-in-out;
}

/* Media Queries */
@media (min-width: 769px){
    .my-custom {
        max-width: 60%;
    }
}
@media (min-width: 1420px){
    .card2{
        padding-bottom: 1.3rem;
    }
}

@media (max-width: 1199px) {
    .event-card{
        margin-left: -10px;
        padding-left: 10px;
    }
    .get-involved{
        max-width: 70%;
    }
}

@media (min-width: 1199px) {
    .vis-illustration{
        margin-top: -100px;
        margin-right: -40px;
        position: relative;
    }
}

@media (max-width: 767px) {
    .vis-illustration{
        display: none;
    }
    .event-illustration{
        display: none;
    }
}

@media (min-width: 1239px) and (max-width: 1419px){
    .card3{
        padding-bottom: 1.3rem;
    }
}

@media (min-width: 1223px) and (max-width: 1419px){
    .card2{
        padding-bottom: 2.6rem;
    }
}

@media (min-width: 1179px) and (max-width: 1222px){
    .card2{
        padding-bottom: 3.9rem;
    }
}

@media (min-width: 1051px) and (max-width: 1222px){
    .card3{
        padding-bottom: 1.3rem;
    }
}

@media (min-width: 1022px) and (max-width: 1178px){
    .card2{
        padding-bottom: 2.6rem;
    }
}

@media (min-width: 992px) and (max-width: 1021px){
    .card2{
        padding-bottom: 3.9rem;
    }
    .card3{
        padding-bottom: 1.25rem;
    }
}

@media (max-width: 991px){
    .nav-center{
        display: flex;
        text-align: center;
        align-items: center;
    }
    .navbar-button{
        max-width: 100px;
        margin-left: -2px !important;
        margin-top: .7rem;
        margin-bottom: .7rem;
    }
}

@media (max-width: 769px) {
    .event-card {
        margin-top: 1rem;
    }
}

@media(min-width: 991px){
    .headline-container{
        -webkit-animation: slide-left .7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
                animation: slide-left .7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    }
}
